// 1) corrige mojibake com try/catch
function fixEncoding(str) {
  try {
    const pct = Array.from(str, (c) => {
      const code = c.charCodeAt(0);
      return code <= 0xff ? "%" + code.toString(16).padStart(2, "0") : c;
    }).join("");
    return decodeURIComponent(pct);
  } catch {
    return str;
  }
}

// 2) s corrige strings que realmente parecem mojibake
function maybeFix(str) {
  return /|[\xA0-\xFF]/.test(str) ? fixEncoding(str) : str;
}

// 3) extrao robusta do nome do curso
function extractCourseName(str) {
  // tira Verbo Jurdico  
  let s = str.replace(/^Verbo Jurdico\s*-\s*/i, "");

  // tenta capturar entre PS-GRADUAO EM e o sufixo (a distncia real, a distncia quebrado ou EAD)
  const m = s.match(/PS-GRADUAO EM\s+(.+?)(?=\s+(?:a\s+dist\S*|EAD)|$)/i);
  if (m) return m[1].trim();

  // fallback genrico:
  s = s
    .replace(/P[o]s[-\s]?Gradua(?:o)?(?:\s+em)?\s*/i, "")
    .replace(/\s+(?:a\s+dist\S*|EAD)$/i, "")
    .trim();
  return s;
}

// 4) composio final, sem mais malformed URI e sem sufixos estranhos
export const getCourseName = (title) => {
  const fixed = maybeFix(title);
  return extractCourseName(fixed);
};
